<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="goodsdetail" v-show="foodshow" ref="goodsDeWrapper">
			<div class="detail-content">
				<div class="image-header">
					<img :src="food.image" alt="">
				</div>
				<div class="cc" @click= "hidefood">
					<i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="normal title">
					
					<div class="left">
						<p class="foodtitle">{{food.name}}</p>
						<p class="sale">
							<span>月售{{food.sellCount}}份</span>&nbsp;
							<span>好评率{{food.rating}}%</span>
						</p>
						<p class="price">
							<span class="newp">￥{{food.price}}</span>&nbsp;
							<span class="oldp" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
						</p>
					</div>
					<addOrdele class="addDel" :food="food" v-if="food.count"></addOrdele>
					<div class="right" v-show="!food.count" @click.stop="changeNum">
						加入购物车
					</div>
				</div>
				<div class="space"></div>
				<div class="introduce">
					<p>商品介绍</p>
					<div class="introduce-content">
						{{food.info}}
					</div>
				</div>
				<div class="space"></div>
				<div class="comment">
					<p>商品评价</p>
					<div class="comment-top">
						<p class="one" @click="Choose('all')" :class="{'comcolor':sizeone}">全部</p>
					 	<p class="two" @click="Choose('sat')" :class="{'comcolor':sizetwo}">满意</p>
					 	<p class="three" @click="Choose('disat')" :class="{'comcolor':sizethree}">不满意</p>
					</div>
					<div class="comment-bottom">
					 	<i class="iconfont icon-duihao" @click="changeColor" :class="{'type':flag}"></i>
					 	&nbsp;
				 		<span>只看有内容的评价</span>
					</div>
				</div>
				<div class="usercomment" ref="ratingsWrapper">
					
						<ul>
							<li  v-for="(v,k) in food.ratings" class="commentli" :key="k" v-show="(jude||v.rateType===data)&&(jude2||v.text!='')">
								<div class="comments">
									<div class="first">
										<span class="time">{{changeTime(v.rateTime)}}</span>
										<p>
											<span class="user">{{v.username}}</span>
											<img :src="v.avatar" alt="" >
										</p>	
									</div>
									<div class="second">
										<i class="iconfont icon-damuzhion" v-show="v.rateType===0"></i>
										<i class="iconfont icon-down" v-show="v.rateType===1"></i>
										<span>{{v.text}}</span>
									</div>
								</div>
							</li>
						</ul>
					
				</div>
			</div>
		</div>
	</transition>
</template>
<script>
	import Vue from "vue"
	import {mapGetters} from "vuex"
	import addOrdele from"../addOrdele/addOrdele.vue"

	export default{
		data(){
			return{
				newTime:[],
				flag:false,
				sizeone:true,
				sizetwo:false,
				sizethree:false,
				jude:true,
				jude2:true,
				data:1
				// chooseData:true
			}
		},
		computed:{
			...mapGetters([
				"goods",
				"food",
				"foodshow"
			]),
			ratLists(){
				if(this.flag){
					return this.food.ratings.filter((item)=>{
						return item.text;
					});
				}
				return this.food.ratings;
			}
		},
		watch:{//监控food.页面，页面加载时进行变化
			food(){
				this.$nextTick(()=>{
					setTimeout(()=>{
						if(!this.gdScroll){
							this.gdScroll = new BScroll(this.$refs.goodsDeWrapper,{
									click:true
							})
						}else{
							this.gdScroll.refresh();//不加refresh数据按2叠加
						};
						this._initComment();
					},100);
				});
			},
		},
		created(){
		// console.log(1);
		},
		methods:{
			hidefood(){
				// console.log(2);
				this.$store.dispatch("hidefood");
			},
			_initGoods(){
				this.gdScroll = new BScroll(this.$refs.goodsDeWrapper,{
					click:true
				});
			},
			_initComment(){
				this.gdScroll2 = new BScroll(this.$refs.ratingsWrapper,{
					click:true
				});
			},
			changeTime(time){
				let time1 = new Date(time);
				let year=time1.getFullYear();
				let month=time1.getMonth();
				let date=time1.getDate();
				let hour=time1.getHours();
				let minute=time1.getMinutes();
				let second=time1.getSeconds();
				function toTwo(a){
					return a<10?"0"+a:a;
				};
				let timechange=year+"-"+toTwo(month)+"-"+toTwo(date)+" "+toTwo(hour)+":"+toTwo(minute)+":"+toTwo(second);
				return timechange;
			},
			changeNum(){
				if(!this.food.count){
					Vue.set(this.food,"count",1);
				}
			},
			Choose(a){
				switch (a) {
					case "all":
						this.jude=true;
						this.sizeone=true;
						this.sizetwo=false;
						this.sizethree=false;
					break;
					case "sat":
						this.jude=false;
						this.data=0;
						this.sizeone=false;
						this.sizetwo=true;
						this.sizethree=false;
					break;
					case "disat":
						this.jude=false;
						this.data=1;
						this.sizeone=false;
						this.sizetwo=false;
						this.sizethree=true;
					break;
					default:
						this.jude=true;
						this.sizeone=true;
						this.sizetwo=false;
						this.sizethree=false;
					break;
				}
				this.doRefresh();
			},
			changeColor(){
				this.flag=!this.flag;
				this.jude2=!this.jude2;
				this.doRefresh();
			},
			doRefresh(){
		        setTimeout(() => {
		            this.gdScroll.refresh()
		            this.gdScroll2.refresh()
		        }, 50)//一定要加延时
		    }
		},
		components:{
			addOrdele
		}
	}
</script>
<style lang="less" scoped>
	.goodsdetail{
		background-color:#fff;
		position:fixed;
		top: 0;
		z-index: 100;
		left: 0;
		right: 0;
		bottom: 0;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.cc{
				position: fixed;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				padding:10px;
				color: white;
				font-size:0.24rem;
			}
			.normal{
				
				width:100%;
				box-sizing:border-box;
				padding:0.36rem;
				
				vertical-align:bottom;
				position:relative;
				
				.left{
					.foodtitle{
						font-size:0.28rem;
						font-weight:700;
						color:rgb(7,17,27);
						padding-bottom:0.16rem;
					}
					.sale{
						padding-bottom:0.36rem;
						font-size:0.2rem;
						color:rgb(147,153,159);
					}
					.price{
						height:0.48rem;
						line-height:0.48rem;
						.newp{
							font-size:0.28rem;
							font-weight:700;
							color:rgb(240,20,20);
						}
						.oldp{
							font-size:0.2rem;
							color:rgb(147,153,159);
							line-height:0.48rem;
							text-decoration:line-through;
						}
					}
				}
				.addDel{
					position:absolute;
					bottom:0.36rem;
					right:0.36rem;
				}
				.right{
					position:absolute;
					bottom:0.36rem;
					right:0.36rem;
					text-align:center;
					line-height:0.48rem;
					font-size:0.2rem;
					color:rgb(255,255,255);
					width:1.48rem;
					height:0.48rem;
					border-radius:0.24rem;
					background-color:rgb(0,160,220);
				}
			}
			.space{
				width:100%;
				background-color: #eee;
				height:0.32rem;
				border-top:0.02rem solid #ddd;
				border-bottom:0.02rem solid #ddd;
			}
			.introduce{
				width:100%;
				box-sizing:border-box;
				padding:0.36rem;
				p{
					font-size:0.28rem;
					// font-weight:700;
					color:rgb(7,17,27);
					padding-bottom:0.12rem;
				}
				.introduce-content{
					box-sizing:border-box;
					padding:0 0.16rem;
					font-size:0.24rem;
					font-weight:200;
					color:rgb(77,85,93);
					line-height:0.48rem;
				}
			}
			.comment{
				width:100%;
				box-sizing:border-box;
				padding:0 0.36rem;
				padding-top:0.36rem;
				border-bottom:0.02rem solid #ddd;
				.comment-top{
					padding:0.36rem 0;
					border-bottom:0.01rem solid rgba(7,17,27,0.1);
					p{
						display: inline-block;
						margin-right:0.16rem;
						width:1rem;
						height:0.5rem;
						text-align:center;
						line-height: 0.5rem;
						border-radius:0.04rem;
					}
					.one{
						background-color: #3399FF;
					}
					.two{
						background-color: #99CCFF;
					}
					.three{
						background-color:#ccc;
					}
					.comcolor{
						color:white;
					}
				}
				.comment-bottom{
					padding:0.36rem 0;
					.icon-duihao{
						font-size:0.28rem;
						color:rgb(147,153,159);
					}
					.type{
						color:red;
					}
					span{
						font-size:0.24rem;
						color:rgb(147,153,159);
					}
				}
			}
			.usercomment{
				width:100%;
				overflow:hidden;
				height:4rem;
				.commentli{
					width:100%;
					box-sizing:border-box;
					padding:0 0.36rem;
					.comments{
						width:100%;
						padding:0.36rem 0;
						border-bottom:0.01rem solid rgba(7,17,27,0.1);
						.first{
							width:100%;
							padding-bottom:0.12rem;
							position:relative;
							.time{
								display:inline-block;
								font-size:0.2rem;
								color:rgb(147,153,159);
								line-height:0.24rem;
								height:0.24rem;
							}
							p{
								display:inline-block;
								position:absolute;
								right:0;
								.user{
									display:inline-block;
									font-size:0.2rem;
									color:rgb(147,153,159);
									line-height:0.24rem;
									height:0.24rem;
									padding-right:0.12rem;
								}
								img{
									width:0.24rem;
									height:0.24rem;
									border-radius:0.12rem;
								}
							}
							
						}
						.second{
							.iconfont{
								height:0.32rem;
								font-size:0.24rem;
								line-height:0.32rem;
								display:inline-block;
								padding-right:0.16rem;
							}
							.icon-damuzhion{
								color:rgb(0,160,220);
							}
							.icon-down{
								color:rgb(183,187,191);
							}
							span{
								height:0.32rem;
								font-size:0.24rem;
								color:rgb(7,17,27);
								line-hieght:0.32rem;
							}
						}
					}
				}
				
			}
		}
	}
</style>



