<template>
	<div class="pright">
		<transition name="move">
			<span v-show = "food.count" class="icon-remove_circle_outline" @click.stop="decrement"></span>&nbsp;
		</transition>
	     <span v-show = "food.count" class="prighttext">{{food.count}}</span>&nbsp;
	     <span class="icon-add_circle" @click.stop="increment"></span>
	</div>	
</template>

<script>
	import Vue from 'vue'
	import {mapGetters} from 'vuex'
	// import 'animate.css/animate.css'

	export default {
		props : ['food','empty'],
		// watch:{
		// 	empty(){
		// 		if(this.empty===0){
		// 			this.food.count=0;
		// 		}
		// 	}	
		// },	
		methods:{
			increment(){
				if(!this.food.count){
					Vue.set(this.food,"count",1);
				}else{
					this.food.count++;
				}
			},
			decrement(){
				if(this.food.count){
					this.food.count--;
				}
			}
		}
	// 	computed:{
	// 		...mapGetters([
	// 			'detailShow',
	// 			'seller',
	// 			'countShow',
	// 			'countInit'
	// 		])
	// 	},
	// 	methods:{
	// 		hideDetail(){
	// 			this.$store.dispatch('hideDetail');
	// 		},
	// 		showCount(i,j){
	// 			this.$store.dispatch('showCount',{
	// 				i,
	// 				j
	// 			});
	// 			// console.log(this.indexI,this.indexJ);
	// 		}
	// 	}
	}
</script>

<style lang="less" scoped>
	.pright{
        font-size: 0.48rem;
        color:#00CCFF;
        .move-enter-active, .move-leave-active {
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        // count>0时候的动画，v-show显示时的动画效果
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0
        }
        .prighttext{
        	font-size: 0.28rem;
        	color:rgb(147,153,159);
        	line-height: 0.40rem;
        }
    }
</style>


