<template>
		
		<transition enter-active-class="animated slideInUp" leave-active-class="animated slideOutDown" >
			<div class="carfoods animated" animate-delay="0s" animate-duration="2s">
				<!-- <div class="animated" animate-delay="0s" animate-duration="2s" ref="carheight"> -->
					
					<div class="carbody carbody-hook">
						<div class="car">
							<div class="cartxt">
								购物车
							</div>
							<div class="clear" @click.stop="emptyFoods">
								清空
							</div>
						</div>
						
						<div class="foodlist" ref="foodScroll">
							<ul>
								<li v-for='v in shopfoods' class="foodli">
									<div class="eachline">
										<span class="name">{{v.name}}</span>
										<div class="price">
											<span class="pri">￥{{v.price}}</span>
											<addOrdele :food='v' class="addOrdele" ></addOrdele>	
										</div>
									</div>	
								</li>
							</ul>	
						</div>

					</div>

				<!-- </div> -->
			
			</div>
		</transition>
</template>

<script>
	import {mapGetters} from "vuex"
	import addOrdele from "../addOrdele/addOrdele.vue"

	export default{
		data(){
			return{
				delate:true,
			}
		},
		props : ['shopfoods'],
		created(){
			this.$nextTick(()=>{
				setTimeout(()=>{
					if(!this.scrollFood){
						this.scrollFood = new BScroll(this.$refs.foodScroll,{
							click:true
						})
					}else{
						this.scrollFood.refresh();
					};//不用wacth监听来加载
					if(this.shopfoods.length==0){
					this.$refs.foodScroll.style.height=50+"px";
					}
				},100)
			});
		},
		watch:{
			shopfoods(){
				if(this.shopfoods.length==0){
					this.$refs.foodScroll.style.height=50+"px";
				}
			}
		},
		components:{
			addOrdele
		},
		methods:{
			_initFood(){
				this.scrollFood = new BScroll(this.$refs.foodScroll,{
					click:true
				}); 
			},
			emptyFoods(){
				this.shopfoods.forEach((element)=> {
					this.$delete(element,"count");//删除count属性
					this.$refs.foodScroll.style.height=50+"px";
				});
			}
			// _changeHeight(){
			// 	if(!this.shopfoods){
			// 		let carth=this.$refs.carheight.getElementsByClassName("carbody-hook")[0];
			// 		carth.style.height=200+"px";
			// 	}
			// }
		}
	}
</script>
<style lang="less" scoped>
	.carfoods{
		width:100%;
		position: fixed;
		z-index:110;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background-color:rgba(7,17,27,0.8);
		.carbody{
			width:100%;
			position:fixed;
			z-index:90;
			// height: 6.2rem;
			bottom:0;
			background-color:white;
			.car{
				width:100%;
				height:0.8rem;
				background-color:#f3f5f7;
				border-bottom:rgba(7,17,27,0.1) 1px solid;
				line-height:0.8rem;
				box-sizing:border-box;
				padding:0 0.36rem;
				display:flex;
				justify-content:space-between;
				.cartxt{
					font-size:0.28rem;
					font-weight:200;
					color:rgb(7,17,27);
				}
				.clear{
					font-size:0.24rem;
					color:rgb(0,160,220);
				}
			}
		}
		.foodlist{
			width:100%;
			height:4.4rem;
			overflow:hidden;
			// display:none;
			.foodli{
				width:100%;
				padding:0 0.36rem;
				box-sizing:border-box;
				.eachline{
					height:0.96rem;
					box-sizing:border-box;
					padding:0.24rem 0;
					display:flex;
					justify-content:space-between;
					border-bottom:rgba(7,17,27,0.1) 1px solid;
					.name{
						display:inline-block;
						font-size:0.28rem;
						color:rgb(7,17,27);
						line-height:0.48rem;
					}
					.price{
						display:inline-block;
						.pri{
							font-size:0.28rem;
							font-weight:500;
							color:rgb(240,20,20);
							line-height:0.48rem;
							display:inline-block;
							padding-right:0.24rem;
						}
						.addOrdele{
							display:inline-block;
						}
					}
				}
			}
		}
	}
</style>


