<template>
	<div class="ratings">
		<div class="title">
			<div class="tit-left">
				<p class="one">{{seller.score}}</p>
				<p class="two">综合评分</p>
				<p class="three">高于周边商家{{seller.rankRate}}%</p>
			</div>
			<div class="tit-right">
				<p class="serviceScore">
					服务态度<star :score="seller.serviceScore" :size="'small'" class="star"></star><span>{{seller.serviceScore}}</span>
				</p>
				<p class="foodScore">
					商品质量<star :score="seller.foodScore" :size="'small'" class="star"></star><span>{{seller.foodScore}}</span>
				</p>
				<p class="arrvialTime">
					送达时间<span class="arrTime">{{seller.deliveryTime}}分钟</span>
				</p>
			</div>
		</div>
		<div class="space"></div>
		<div class="comment">
			 <div class="comment-top">
			 	<p class="one" @click="Choose('all')" :class="{'comcolor':sizeone}">全部&nbsp;{{getAllNum}}</p>
			 	<p class="two" @click="Choose('sat')" :class="{'comcolor':sizetwo}">满意&nbsp;{{getSatNum}}</p>
			 	<p class="three" @click="Choose('disat')" :class="{'comcolor':sizethree}">不满意&nbsp;{{getAllNum-getSatNum}}</p>
			 </div>
			 <div class="comment-bottom">
			 	<i class="iconfont icon-duihao" @click="changeColor" :class="{'type':flag}"></i>
			 	&nbsp;
			 	<span>只看有内容的评价</span>
			 </div>
		</div>
		<div class="ratings" ref="ratScroller">
			<ul>
				<li v-for="(v,k) in ratings" :key="k" v-show="(jude||v.rateType===data)&&(jude2||v.text!='')" class="ratingsLi">
					<div class="left-rat">
						<img :src="v.avatar" alt="">
					</div>
					<div class="right-rat">
						<p class="one-line">
							<span class="user">{{v.username}}</span>
							<span class="time">{{changeTime(v.rateTime)}}</span>
						</p>
						<p class="two-line">
							<star :score="v.score" :size="'smaller'" class="star2"></star>
							<span v-if="v.deliveryTime">{{v.deliveryTime}}分钟到达</span>
						</p>
						<p class="three-line">
							{{v.text}}
						</p>
						<div class="four-line">
							<i class="iconfont icon-damuzhion" v-show="v.rateType===0"></i>
							<i class="iconfont icon-down" v-show="v.rateType===1"></i>
							<p v-for="(v1,k) in v.recommend">
								<span v-if="k<3" class="remd">{{v1}}</span>
							</p>
						</div>
					
					</div>
				</li>
				<!-- <li class="space1"></li> -->
			</ul>
			
		</div>
	</div>
</template>

<script>
	import {mapGetters} from "vuex"
	import Star from "../star/Star.vue"
	export default {
		data(){
			return{
				newTime:[],
				jude:true,
				jude2:true,
				data:1,
				flag:false,
				sizeone:true,
				sizetwo:false,
				sizethree:false
			}
		},
		computed:{
			...mapGetters([
				'seller',
				'ratings'//引入的时候可以定义type
			]),
			// changeTime(){
			// 	console.log(this.ratings);
			// 	let timeNum = this.ratings;
			// 	console.log(timeNum);
			// 	for(let i in timeNum){
			// 		let time1 = new Date(timeNum[i].rateTime);
			// 		let year=time1.getFullYear();
			// 		let month=time1.getMonth();
			// 		let date=time1.getDate();
			// 		let hour=time1.getHours();
			// 		let minute=time1.getMinutes();
			// 		let second=time1.getSeconds();
			// 		let timechange=year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
			// 		this.newTime.push(timechange);
			// 	}
			// 	return this.newTime;
			// }
			getAllNum(){
				return this.ratings.length;
			},
			getSatNum(){
				let arr=[];
				this.ratings.forEach((a)=>{//foreach只能遍历数组
					if(a.rateType===0){
						arr.push(a);
					}
				});
				return arr.length;
			}
		},
		created(){
			this.$store.dispatch("getSeller");
			this.$store.dispatch("getRatings");
			this.$nextTick(()=>{
				setTimeout(()=>{
					this._initRat();
				},100)
			});
		},
		components:{
			"star":Star
		},
		methods:{
			_initRat(){
				this.ratScroll = new BScroll(this.$refs.ratScroller,{
					click:true
				});
			},
			changeTime(time){
				let time1 = new Date(time);
				let year=time1.getFullYear();
				let month=time1.getMonth();
				let date=time1.getDate();
				let hour=time1.getHours();
				let minute=time1.getMinutes();
				let second=time1.getSeconds();
				function toTwo(a){
					return a<10?"0"+a:a;
				};
				let timechange=year+"-"+toTwo(month)+"-"+toTwo(date)+" "+toTwo(hour)+":"+toTwo(minute)+":"+toTwo(second);
				return timechange;
			},
			Choose(a){
				switch (a) {
					case "all":
						this.jude=true;
						this.sizeone=true;
						this.sizetwo=false;
						this.sizethree=false;
					break;
					case "sat":
						this.jude=false;
						this.data=0;
						this.sizeone=false;
						this.sizetwo=true;
						this.sizethree=false;
					break;
					case "disat":
						this.jude=false;
						this.data=1;
						this.sizeone=false;
						this.sizetwo=false;
						this.sizethree=true;
					break;
					default:
						this.jude=true;
						this.sizeone=true;
						this.sizetwo=false;
						this.sizethree=false;
					break;
				}
				this.doRefresh();
			},
			changeColor(){
				this.flag=!this.flag;
				this.jude2=!this.jude2;
				this.doRefresh();
			},
			doRefresh(){
		        setTimeout(() => {
		            this.ratScroll.refresh()
		        }, 50)//一定要加延时
		    }
		}
	}	
</script>

<style lang='less' scoped>
	.ratings{
		width:100%;
		margin-bottom:1.2rem;
		.title{
			width:100%;
			box-sizing:border-box;
			padding:0.36rem 0;
			display:flex;
			.tit-left{
				width:2.75rem;
				border-right:0.01rem solid rgba(7,17,27,0.1);
				text-align:center;
				.one{
					font-size:0.48rem;
					color:rgb(255,153,0);
					height:0.56rem;
					line-height: 0.56rem;
					padding-bottom:0.12rem;
				}
				.two{
					font-size:0.24rem;
					color:rgb(7,17,27);
					padding-bottom:0.16rem;
				}
				.three{
					font-size:0.2rem;
					color:rgb(147,153,159);
					padding-bottom:0.12rem;
				}

			}
			.tit-right{
				width:100%;
				flex:1;
				box-sizing:border-box;
				padding:0 0.48rem;
				p{
					font-size:0.24rem;
					color:rgb(7,17,27);
					padding-bottom:0.16rem;
					height:0.36rem;
					line-height:0.36rem;
					vertical-align:top;
					span{
						color:rgb(255,153,0);
					}
					.star{
						padding:0 0.24rem;
						display:inline-block;
						padding-bottom:0.12rem;
					}
					.arrTime{
						padding:0 0.24rem;
						font-size:0.24rem;
						color:rgb(147,153,159);
					}
				}
				p:last-child{
					padding-bottom:0rem;
				}
			}
		}
		.space{
			width:100%;
			background-color: #eee;
			height:0.32rem;
			border-top:0.02rem solid #ddd;
			border-bottom:0.02rem solid #ddd;
		}
		.comment{
			width:100%;
			box-sizing:border-box;
			padding:0 0.36rem;
			border-bottom:0.02rem solid #ddd;
			.comment-top{
				padding:0.36rem 0;
				border-bottom:0.01rem solid rgba(7,17,27,0.1);
				p{
					display: inline-block;
					margin-right:0.16rem;
					width:1.2rem;
					height:0.5rem;
					text-align:center;
					line-height: 0.5rem;
					border-radius:0.04rem;
				}
				.one{
					background-color: #3399FF;
				}
				.two{
					background-color: #99CCFF;
				}
				.three{
					background-color:#ccc;
				}
				.comcolor{
					color:white;
				}
			}
			.comment-bottom{
				padding:0.36rem 0;
				.icon-duihao{
					font-size:0.28rem;
					color:rgb(147,153,159);
				}
				.type{
					color:red;
				}
				span{
					font-size:0.24rem;
					color:rgb(147,153,159);
				}
			}
		}
		.ratings{
			height:4rem;
			width:100%;
			box-sizing:border-box;
			overflow: hidden;
			padding:0 0.36rem;
			ul{
				.ratingsLi{
					width:100%;
					padding:0.36rem 0;
					border-bottom:solid rgba(7,17,27,0.1) 0.01rem;
					display:flex;
					.left-rat{
						width:0.56rem;
						height:0.56rem;
						img{
							width:0.56rem;
							height:0.56rem;
							border-radius:0.28rem;
						}
					}
					.right-rat{
						flex:1;
						padding-left:0.24rem;
						.one-line{
							display:flex;
							width:100%;
							justify-content:between-space;
							padding-bottom:0.08rem;
							position:relative;
							.user{
								display:inline-block;
								font-size:0.2rem;
								color:rgb(7,17,27);
								line-height:0.24rem;
							}
							.time{
								position:absolute;
								right:0;
								display:inline-block;
								font-size:0.2rem;
								font-weight:200;
								color:rgb(147,153,159);
								line-height:0.24rem;
							}
						}
						.two-line{
							padding-bottom:0.12rem;
							.star{
								display:inline-block;
								padding-right:0.12rem;
							}
							span{
								font-size:0.2rem;
								font-weight:200;
								color:rgb(147,153,159);
							}
						}
						.three-line{
							font-size:0.24rem;
							color:rgb(7,17,27);
							line-height:0.36rem;
							padding-bottom:0.16rem;
						}
						.four-line{
							.iconfont{
								font-size:0.24rem;
								line-height:0.32rem;
								display:inline-block;
								padding-right:0.16rem;
							}
							.icon-damuzhion{
								color:rgb(0,160,220);
							}
							.icon-down{
								color:rgb(183,187,191);
							}
							p{
								display:inline-block;
								span{
									display:inline-block;
									font-size:0.18rem;
									color:rgb(147,153,159);
									line-height:0.32rem;
									height:0.32rem;
									padding:0 0.12rem;
									border:1px solid rgba(7,17,27,0.1);
									background-color:rgb(255,255,255);
									border-radius:2px;
									margin-right:0.16rem;
								}
							}
						}
					}
				}
				
			}
			
		}
	}
</style>


