<template>
	<div class="seller" ref="sellerScroll">
		<div><!-- 内部一定要嵌套一个整的div标签 -->
			<div class="title normal">
				<div class="title-cont normal1">
					<div class="left">
						<p>{{seller.name}}</p>
						<star class="star" :score = "seller.score" :size="'middle'"></star>
						<span>({{seller.ratingCount}})&nbsp;&nbsp;月售{{seller.sellCount}}单</span>
					</div>
					<div class="shoucang">
						<i class="iconfont icon-xin" :class="{'iconColor':bgChange}" @click="shouCang"></i>
						<p class="text">{{bgChange?"收  藏":"已收藏"}}</p>
					</div>
				</div>
			</div>

			<div class="data normal">
				<div class="data-cont normal1">
					<div class="one">
		    			<p class="pone">起送价</p>
		    			<p class="ptwo">
		    			     <span class="value">{{seller.minPrice}}</span>
		    			     <span class="danwei">元</span>
		    			</p>
		    		</div>
		    		<div class="two">
		    			<p class="pone">商家配送</p>
		    			<p class="ptwo">
		    			     <span class="value">{{seller.deliveryPrice}}</span>
		    			     <span class="danwei">元</span>
		    			</p>
		    		</div>
		    		<div class="three">
		    			<p class="pone">平均配送时间</p>
		    			<p class="ptwo">
		    			     <span class="value">{{seller.deliveryTime}}</span>
		    			     <span class="danwei">分钟</span>
		    			</p>
		    		</div>
				</div>
			</div>

			<div class="space"></div>
			<div class="normal act">
				<div class="normal1 acttop">
					<p>公告与活动</p>
					<div class="actcont">{{seller.bulletin}}</div>
				</div>
				
				<ul>
					<li class="actlist" v-for="(v,index) in seller.supports">
						<img :src="url[index]" alt="">
						<span>{{v.description}}</span>
					</li>
				</ul>
				
			</div>
			<div class="space"></div>
			<div class="normal img">
				<div class="normal1 imgbox">
					<p>商家实景</p>

					<div ref="imgScroll" class="imgBody"> 
						<ul ref = 'pics'>
							<li class="imgli" v-for="(v,k) in seller.pics" >
								<img :src="v" width="120" height="80">
							</li>
						</ul>
					</div>

				</div>
			</div>
			<div class="space"></div>
			<div class="normal infor">
				<div class="normal1 inforbox">
					<p class="infortitle">商家信息</p>
					<ul>
						<li v-for="(v,k) in seller.infos" class="inforli">
							<p>{{v}}</p>
						</li>
					</ul>
				</div>
			</div>

			<div style="height:40px;width:100%;"></div>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from "vuex"
	import Star from "../star/Star.vue"
	export default{
		data(){
			return{
				url:[
					"../../../static/imgs/decrease.png",
					"../../../static/imgs/discount.png",
					"../../../static/imgs/special.png",
					"../../../static/imgs/invoice.png",
					"../../../static/imgs/guarantee.png"
				],
				bgChange:false
			}	
		},
		created(){
			this.$nextTick(()=>{//nextTick有200毫秒延迟
				setTimeout( () => {
					this._initPage();
					this._initImg();
			},100)
			});
			this.$store.dispatch("getSeller");
		},
		computed:{
			...mapGetters([
				'seller',
			])
		},
		components:{
			"star":Star
		},
		methods:{
			_initPage(){
				this.pageScroll = new BScroll(this.$refs.sellerScroll,{
					click:true
				});
			},
			_initImg(){
				let width = 120 ;
				let margin = 6.624;
				this.$refs.pics.style.width = ( width + margin ) * this.seller.pics.length - margin + 'px';
				this.imageScroll = new BScroll(this.$refs.imgScroll,{
					scrollX:true,
					scrollY:false,
					click:true
				});
			},
			shouCang(){
				this.bgChange=!this.bgChange;
			}
		}
	}
</script>
<style lang="less" scoped>
	.seller{
		position:absolute;
		width:100%;
		overflow:hidden;
		top:3.48rem;
		bottom:0rem;
		// z-index:-2;
		.normal{
			width:100%;
			box-sizing: border-box;
			padding:0 0.36rem;
			.normal1{
				width:100%;
				padding:0.36rem 0;
			}
			.title-cont{
				display:flex;
				justify-content:space-between;
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				.left{
					p{
						font-size:0.28rem;
						color:rgb(7,17,27);
						padding-bottom:0.16rem;
					}
					.star{
						display:inline-block;
					}
					span{
						font-size:0.2rem;
						color:rgb(77,85,93);
						line-height:0.36rem;
						display:inline-block;
						height:0.36rem;
					}
				}
				.shoucang{
					text-align:center;
					width:0.8rem;
					.icon-xin{
						font-size:0.48rem;
						color:#ccc;
						margin-bottom:0.08rem;
						// &.iconColor{
						// 	color:#ccc;
						// }
					}
					.iconColor{
						color:rgb(240,20,20);
					}
					.text{
						font-size:0.2rem;
						color:rgb(77,85,93);
					}
				}	
			}
			.data-cont{
				display:flex;
				div{
					flex:1;
					text-align:center;
					.pone{
						font-size:0.2rem;
						color:rgb(147,153,159);
						padding-bottom:0.08rem;
					}
					.ptwo{
						font-weight:200;
						color:rgb(7,17,27);
						}
						.value{
							font-size:0.48rem;
							
						.danwei{
							font-size:0.2rem;
							vertical-align:baseline;
						}
					}
				}
				.two{
					border-left:0.01rem solid rgba(7,17,27,0.1);
					border-right:0.01rem solid rgba(7,17,27,0.1);
				}
			}
			.acttop{
				p{
					font-size:0.28rem;
					color:rgb(7,17,27);
					padding-bottom:0.16rem;
				}
				div{
					width:100%;
					box-sizing:border-box;
					padding:0 0.24rem;
					font-size:0.24rem;
					font-weight:200;
					color:rgb(240,20,20);
					line-height: 0.48rem;
					padding-bottom:0.32rem;
				}
			}
			ul{
				.actlist{
					border-top:0.01rem solid rgba(7,17,27,0.1);
					padding:0.32rem 0.24rem;
					img{
						width:0.32rem;
						height:0.32rem;
					}
				}
			}
		}
		.space{
			width:100%;
			background-color: #eee;
			height:0.32rem;
			border-top:0.02rem solid #ddd;
			border-bottom:0.02rem solid #ddd;
		}
		.img{
			.imgbox{
				p{
					font-size:0.28rem;
					color:rgb(7,17,27);
					padding-bottom:0.16rem;
				}
				.imgBody{
					white-space: nowrap;
					.imgli{
						overflow:hidden;
						display:inline-block;
						width:2.4rem;
						height:1.8rem;
						margin-right:0.12rem;
						img{
							width:2.4rem;
							height:1.8rem;
						}
					}
				}
				
			}
		}
		.infor{
			.inforbox{
				.infortitle{
					font-size:0.28rem;
					color:rgb(7,17,27);
					padding-bottom:0.24rem;
				}
				.inforli{
					width:100%;
					border-top:0.01rem solid rgba(7,17,27,0.1);
					box-sizing:border-box;
					padding:0.32rem 0.24rem;
					p{
						font-size:0.24rem;
						font-weight:200;
						color:rgb(7,17,27);
						line-heigth:0.32rem;
					}
				}
			}
		}	
	}
</style>


