<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="detail" v-show="detailshow">
			<p class="title">{{seller.name}}</p>
			<div class="starts">
				<star :score = "seller.score" :size="'large'"></star>
			</div>
			<div class="sec-title">
				<p class="line"></p>
				<p class="infor-title">优惠信息</p>
				<p class="line"></p>
			</div>

			<div class="list">
				<ul>
					<li v-for="(v,k) in seller.supports" :key="k">
						<img :src="url[k]" alt="">
						<span>{{v.description}}</span>
					</li>
				</ul>
			</div>

			<div class="sec-title">
				<p class="line"></p>
				<p class="infor-title">商家公告</p>
				<p class="line"></p>
			</div>

			<div class="content">
				{{seller.bulletin}}
			</div>

			<div class="bottom-box">
				<i class="iconfont icon-X" @click="close"></i>
			</div>
			
		</div>
	</transition>
</template>

<script>
	import {mapGetters} from "vuex"
	import Star from "../star/Star.vue"

	export default {
		data(){
			return {
				url:[
					"../../../static/imgs/decrease.png",
					"../../../static/imgs/discount.png",
					"../../../static/imgs/discount.png",
					"../../../static/imgs/special.png",
					"../../../static/imgs/special.png"
				]
			}
		},
		computed : {
			...mapGetters([
				"detailshow",
				"seller"
			])
		},
		components:{
			"star":Star
		},
		methods:{
			close(){
				this.$store.dispatch("closedetail");
			}
		}
	}
</script>

<style lang="less" scoped>
	.detail{
		width:100%;
		position: fixed;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background-color:rgba(7,17,27,0.8);
		// filter:blur(10px);
		padding-top:1.28rem;
		z-index:200;
		.title{
			width:4rem;
			margin:0 auto;
			text-align: center;
			font-size:0.32rem;
			font-weight:700;
			color:rgb(255,255,255);
			line-height:0.32rem;
		}
		.starts{
			width:3.6rem;
			margin:0 auto;
			padding-top:0.32rem;
			padding-bottom: 0.56rem;
		}
		.sec-title{
			width:100%;
			box-sizing:border-box;
			padding:0 0.72rem;
			margin-bottom:0.48rem;
			p{	
				display: inline-block;
			}
			.infor-title{
				padding:0 0.16rem;
				font-size:0.28rem;
				font-weight:600;
				color:rgb(255,255,255);
				line-height:0.32rem;
			}
			.line{
				width:2.24rem;
				height:0.02rem;
				background-color:rgba(255,255,255,0.2);
			}
		}
		.list{
			padding-left:0.96rem;
			li{
				margin-bottom:0.24rem;
				img{
					width:0.32rem;
				}
				span{
					padding-left:0.12rem;
					font-size:0.24rem;
					font-weight:200;
					color:rgb(255,255,255);
					line-height:0.24rem;
				}
			}
		}
		.content{
			width:100%;
			box-sizing:border-box;
			padding:0 0.96rem;
			padding-right:0.96rem;
			font-size:0.24rem;
			font-weight:200;
			color:rgb(255,255,255);
			line-height:0.48rem;
		}
		.bottom-box{
			width:100%;
			position:fixed;
			text-align:center;
			bottom:0.64rem;
			.iconfont{
				color:rgb(255,255,255,0.5);
				font-size:0.64rem;
			}
		}
	}
</style>


