<template>
	<div class="star" :class="sizeType">
		<span class="eachstar" v-for="v in starItem" :class="v"></span>
	</div>
</template>

<script>
	const LENGTH = 5;
	const ON_STAR = "on";
	const HALF_STAR = "half";
	const OFF_STAR = "off";

	export default {
		props:['score','size'],
		computed:{
			sizeType(){
				return "star-"+this.size;
			},
			starItem(){
				let arr = [];
				let num = this.score;
				let intnum = Math.floor(num);
				let floatnum = num - intnum;
				for(let i=0;i<intnum;i++){
					arr.push(ON_STAR);
				}
				if(floatnum>=0.5){
					arr.push(HALF_STAR);
				}
				while(arr.length<LENGTH){
					arr.push(OFF_STAR);
				}
				return arr;
			}
		}
	}
</script>

<style lang="less" scoped>
	.star{
		// font-size:1rem;
		// .eachstar{
		// 	display:inline-block;
		// 	width:0.48rem;
		// 	height:0.48rem;
		// 	margin-right:0.24rem;
		// 	&.on{
		// 		background-image: url("star_on.png");
		// 		background-size: 100% 100%;
		// 		background-repeat: no-repeat;
		// 	}
		// 	&.off{
		// 		background-image: url("star_off.png");
		// 		background-size: 100% 100%;
		// 		background-repeat: no-repeat;
		// 	}
		// 	&.half{
		// 		background-image: url("star_half.png");
		// 		background-size: 100% 100%;
		// 		background-repeat: no-repeat;
		// 	}
		// }
		&.star-large{
			.eachstar{
				display:inline-block;
				width:0.48rem;
				height:0.48rem;
				margin-right:0.24rem;
				&.on{
					background-image: url("star_on.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url("star_off.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url("star_half.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
			}
		}
		&.star-middle{
			.eachstar{
				display:inline-block;
				width:0.36rem;
				height:0.36rem;
				margin-right:0.1rem;
				&.on{
					background-image: url("star_on.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url("star_off.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url("star_half.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
			}
		}
		&.star-small{
			.eachstar{
				display:inline-block;
				width:0.28rem;
				height:0.28rem;
				margin-right:0.1rem;
				&.on{
					background-image: url("star_on.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url("star_off.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url("star_half.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
			}
		}
		&.star-smaller{
			.eachstar{
				display:inline-block;
				width:0.2rem;
				height:0.2rem;
				margin-right:0.1rem;
				&.on{
					background-image: url("star_on.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url("star_off.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url("star_half.png");
					background-size: 100% 100%;
					background-repeat: no-repeat;
				}
			}
		}
	}
</style>


