<template>
	<div class="header">
		<div class="top-box">
			<div class="bgImg">
				<img :src="seller.avatar" alt="">
			</div>

			<div>
				<img :src="seller.avatar" alt="" class="avatar" @click='show'>
			</div>

			<div class="right-box">
				<ul>
					<li>
						<img src="../../../static/imgs/brand.png" alt="" class="brand">
						<span class="title">{{seller.name}}</span>
					</li>
					<li>
						<span>{{seller.description}}</span>
						<span>{{seller.deliveryTime}}分钟送达</span>
					</li>
					<li>
						<img src="../../../static/imgs/decrease.png" alt="">
						<span v-if="seller.supports">{{seller.supports[0].description}}，满50减10</span>
					</li>
				</ul>
			</div>

			<div class="righttext" v-if="seller.supports">
				<p>{{seller.supports.length}}个 ></p>
			</div>
		</div>

		<div class="bottombox">
			<img src="../../../static/imgs/bulletin.png" alt="">
			<span class="bulletin">{{seller.bulletin}}</span>
			<span class="openit">></span>
		</div>
        <Detail></Detail>
	</div>
</template>
<script>
	import {mapGetters} from "vuex"
	import Detail from "../detail/Detail.vue"

	export default {
		computed:{
			...mapGetters([
					'seller',
				])
		},
		components:{
			Detail,
		},
		created(){
			this.$store.dispatch("getSeller");
		},
		methods:{
			show(){
				this.$store.dispatch("showdetail");
			}
		}
	}
</script>

<style lang="less" scoped>
	.header{
		width:100%;
		height:2.68rem;
		background-color:rgba(7,17,27,0.5);
		overflow:hidden;
		position:relative;
		.bgImg{
			position:absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			z-index:-1;
			filter:blur(5px);
			img{
				width:100%;
				height:100%;
			}
		}
		.top-box{
			width:100%;
			box-sizing:border-box;
			display:flex;
			padding:0.48rem 0 0.3rem 0;
			padding-left:0.48rem;
			.avatar{
				width: 1.28rem;
				height: 1.28rem;
				border-radius:0.04rem;
			}
			.right-box{
				margin-left:0.32rem;
				li:nth-of-type(1){
					margin-top:0.04rem;
					margin-bottom:0.16rem;
					img{
						width:0.6rem;
						height:0.36rem;
						margin-right:0.12rem;
					}
					span{
						font-size:0.32rem;
						color:rgb(255,255,255);
						font-weight:bold;
						line-height:0.36rem;
					}
				}
				li:nth-of-type(2){
					margin-bottom:0.2rem;
					span{
						font-size:0.24rem;
						color:rgb(255,255,255);
						font-weight:200;
						line-height:0.24rem;
					}
				}
				li:nth-of-type(3){
					margin-top:0.04rem;
					img{
						width:0.32rem;
						height:0.32rem;
						margin-right:0.08rem;
					}
					span{
						font-size:0.2rem;
						color:rgb(255,255,255);
						font-weight:200;
						line-height:0.24rem;
					}
				}
			}
			.righttext{
				text-align:center;
				font-size:0.2rem;
				color:rgb(255,255,255);
				font-weight:200;
				line-height:0.48rem;
				width:0.96rem;
				height:0.48rem;
				border-radius:0.24rem;
				background:rgba(0,0,0,0.2);
				position:fixed;
				top:1.4rem;
				right:0.25rem;
			}
		}

		.bottombox{
			// position:absolute;
			// bottom:0;
			width:100%;
			height:0.56rem;
			line-height:0.56rem;
			background:rgba(7,17,27,0.5);
			color:rgb(255,255,255);
			img{
				width:0.6rem;
				margin-left:0.24rem;
				margin-right:0.08rem;
			}
			.bulletin{
				font-size:0.2rem;
				width:80%;
				font-weight:200;
				line-height: 0.56rem;
				overflow:hidden;
				white-space:nowrap;
				text-overflow:ellipsis;
				display:inline-block;
			}
			.openit{
				width:5%;
				text-align: center;
				display:inline-block;
			}
		}
	}
</style>


