<template>
	<div class="goods">
		<div class="goods-menu" ref ="menuWrapper1">
			<ul>
				<li v-for="(v,k) in goods" v-bind:key="k" @click="selectMenu(k,$event)" :class="{'current':k === currentIndex}">
					<div>
						<span>{{v.name}}</span>
					</div>
				</li>
			</ul>
		</div>
		<div class="goods-foods" ref="menuWrapper2">
			<div>
				<div v-for="(v,k) in goods" v-bind:key="k" class="food-list food-list-hook">
					<div class="title">{{v.name}}</div>
					<ul>
						<li v-for="(v1,k1) in v.foods" v-bind:key="k1"  @click = 'showfood(v1,$event)'>
							<div class="content">
								<img :src="v1.image">
								<div >
									<p class="sectitle">{{v1.name}}</p>
									<p v-if="v1.description" class="contsize">{{v1.description}}</p>
									<p class="contsize">月售<span>{{v1.sellCount}}</span>份&nbsp好评率<span>{{v1.rating}}</span>%</p>
									<p class="price">
										
											<span class="newP">￥{{v1.price}}</span>
											<span v-if="v1.oldPrice" class="oldP">￥{{v1.oldPrice}}</span>
										
										
									</p>
								</div>
								<addOrdele class="adddel" :food="v1"></addOrdele>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>

		<v-goodsdetail></v-goodsdetail>
	</div>

</template>
<script>
	import {mapGetters} from "vuex"
	import goodsDetail from "../detail/goodsDetail.vue"
	import addOrdele from"../addOrdele/addOrdele.vue"
	import shopcart from"../Shopcart/Shopcart.vue"

	export default {
		data(){
			return {
				listHeight:[],
				scrollY:0
			}
		},
		computed:{
			...mapGetters([
				"goods",
			]),
			currentIndex(){
				for(let i=0;i<this.listHeight.length;i++){
					let height1 = this.listHeight[i];
					let height2 = this.listHeight[i+1];
					if(this.scrollY >=height1&&this.scrollY<height2){
						return i;
					}
				}
				return 0;
			}
		},
		created(){
			this.$store.dispatch("getGoods");
			this.$nextTick(()=>{
				// console.log(this.calcHeight);
				setTimeout(()=>{
					this._initMenu();
					this._calcHeight();
					this._initFood();
				},100);
			});
			// this.$store.dispatch("showfood",f);
		},
		methods:{
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper1,{click:true});//不加click,无法对scroll里面的内容进行点击
				this.menuBS = new BScroll(this.$refs.menuWrapper2);
			},
			selectMenu(key,e){
				if(!e._constructed){
					return;
				}
				let foodList = this.$refs.menuWrapper2.getElementsByClassName("food-list-hook");
				let el=foodList[key];
				this.foodBS.scrollToElement(el,300);
			},
			_calcHeight(){
				let foodList = this.$refs.menuWrapper2.getElementsByClassName("food-list-hook");
				let height=0;
				this.listHeight.push(height);
				for(let i=0;i<foodList.length;i++){
					height +=foodList[i].clientHeight;
					this.listHeight.push(height);
				}
				// console.log(this.listHeight);
			},
			_initFood(){
				this.foodBS = new BScroll(this.$refs.menuWrapper2,{
					probeType:3,
					click : true//如果不加，单个li无法触发
				});
				this.foodBS.on("scroll",(pos)=>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})
			},
			showfood(f,e){
				if(!e._constructed){
					return;
				};
				this.$store.dispatch("showfood",f);
				// console.log(ok);
			}
		},
		components:{
			"v-goodsdetail":goodsDetail,
			addOrdele,
			shopcart
		}
		
	}
</script>

<style lang="less" scoped>
	.goods{
		position:absolute;
		top:3.48rem;
		bottom:0.92rem;
		width:100%;
		display:flex;
		// z-index:-2;//加的话，goodsDetail页面有问题
		overflow:hidden;
		font-size:0.24rem;
		.goods-menu{
			flex:0 0 1.6rem;
			width: 1.6rem;
			background-color:#f3f5f7;
			overflow:hidden;
			li{
				&.current {
					background:#fff;
				}
				width:100%;
				font-size:0.24rem;
				font-weight:200;
				div{
					height:1.08rem;
					width:1.12rem;
					margin:0 auto;
					display:table;
					border-bottom:0.01rem solid #ccc;
					span{
						text-align:center;
						vertical-align: middle;
						display: table-cell;
						width:1.12rem;
						line-height:0.3rem;
					}
				}
				
			}
			li:last-child{
				div{
					border-bottom:none;
				}
			}
			
		}
		.goods-foods{
			width:100%;
			flex:1;
			overflow:hidden;
			.title{
				height:0.52rem;
				width:100%;
				box-sizing: border-box;
				border-left:0.04rem solid #d9dde1;
				padding-left:0.28rem;
				font-size:0.24rem;
				line-height:0.52rem;
				color:rgb(147,153,159);
				background:#f3f5f7;
			}
			li{
				width:100%;
				box-sizing: border-box;
				padding:0.36rem;
				padding-bottom:0rem;
				.content{
					width:100%;
					padding-bottom:0.36rem;
					border-bottom:0.02rem solid rgba(7,17,27,0.1);
					display:flex;
					position:relative;
					img{
						width:1.08rem;
						height:1.08rem;
					}
					div{
						display:inline-block;
						flex:1;
						margin-left:0.2rem;
						padding-top:0.04rem;
						vertical-align: top;
						.sectitle{
							font-weight:500;
							font-size:0.28rem;
							color:rgb(77,85,93);
							margin-bottom:0.16rem;
						}
						.contsize{
							font-size:0.2rem;
							height:0.2rem;
							width:60%;
							color:rgb(147,153,159);
					
							text-overflow:ellipsis;
							white-space:nowrap;
							overflow:hidden;
							margin-bottom:0.16rem;
						}
						.price{
							height:0.28rem;
							font-size:0.28rem;
							color:rgb(147,153,159);
							font-size:600;
							padding-right:0.16rem;
							line-height:0.28rem;
							// padding-bottom:0.36rem;
							// display:flex;
							// justify-content:space-between;
								.newP{
									color:red;
									padding-right:0.16rem;
								}
								.oldP{
									font-size:0.2rem;
									text-decoration:line-through;
								}
								
							
						}
					}
					.adddel{
						position:absolute;
						right:0;
						bottom:0.2rem;
					}
				}
			}
			li:last-child{
				.content{
					border:none;
				}
			}
		}
	}	
</style>


