<template>
    <div>
        <div class="shopcart">
            <div class="content">
                <div class="content-left">
                    <div class="logo-wrap">
                        <div class="logo" @click="showCar" :class="{'highlight':show}">
                            <i class="icon iconfont icon-gouwuche" :class="{'colorchange':show}"></i>
                        </div>
                        <div class="num">{{totalCount}}</div>
                    </div>
                    <div class="price">￥{{totalPrice}}</div>
                    <div class="desc">另需配送费￥4元</div>
                </div>
                <div class="content-righ" @click.stop.prevent = 'pay'>
                    <div class="pay" :class='{"enough":totalPrice >= seller.minPrice}'>
                        {{payDesc}}
                    </div>
                </div>
            </div>    
        </div> 
        <v-carfoods :shopfoods="selectFoods" v-if="show&&totalCount"></v-carfoods><!-- 这里不能用v-show,v-show在页面之前加载DOM -->
    </div>
</template>

<script>   
    import {mapGetters} from 'vuex'
    import carfoods from "../carfoods/carfoods.vue"
    export default {
        data(){
            return{
                show:false,
                datachange:1
            }
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods',
                
            ]),
            selectFoods(){
                let foods = [];
                this.goods.forEach( good => {//foreach只能循环数组
                    good.foods.forEach( food => {
                        if (food.count > 0){
                            foods.push(food);
                        }
                    }) 
                });
                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach(food => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach(food => {
                    count += food.count;
                });
                if(count===0){
                    this.datachange++;
                };
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0 ){
                    return `￥${this.seller.minPrice}元起送`
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算"
                }

            }
        },
        watch:{
           datachange:function(){//监视computed和data数据变化
                // console.log(11);
                this.show=false;
           }
        },
        methods : {
            showCar(){
                if(this.totalCount!=0){
                     this.show=!this.show;
                }else{

                }
            }
        },
        components:{
            "v-carfoods":carfoods
        }
    };

</script>

<style lang="less" type="text/less">
    .shopcart{
        height:0.96rem;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 120;
        .content{
            display: flex;
            background: #141d27;
            // font-size: 0;
            .content-left{
                flex:1;   
                .logo-wrap{
                    display: inline-block;
                    vertical-align: top;
                    position: relative;
                    top: -0.2rem;
                    margin-left: 0.24rem;
                    margin-bottom: 0.04rem;
                    padding:0.12rem;
                    background: #141d27;
                    border-radius: 50%;
                    box-sizing: border-box;
                    .logo{
                        width: 0.88rem;
                        height: 0.88rem;
                        line-height: 0.88rem;
                        text-align: center;
                        // background: white;
                        background-color: #2b343c;
                        &.highlight{//&要放在符号内部才能应用
                            background:#00a0dc;
                        }
                        border-radius: 50%;
                        i{
                            font-size: 0.46rem;
                            color:rgba(255,255,255,0.4);
                           &.colorchange{
                                color: white;
                            }
                        }
                        
                    }
                    .num{
                        position: absolute;
                        top:0;
                        right: 0;
                        width: 0.48rem;
                        height: 0.32rem;
                        line-height: 0.37rem;
                        text-align: center;
                        border-radius: 0.16rem;
                        font-size: 0.09rem;
                        font-weight: 700;
                        color:white;
                        background: rgb(240,20,20);
                        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
                    }
                }   
                .price{
                    display: inline-block;
                    font-size: 0.32rem;
                    font-weight: 700;
                    line-height: 0.58rem;
                    color:#fff;
                    // vertical-align: top;
                    margin-top: 0.24rem;
                    box-sizing: border-box;
                    padding-right: 0.24rem;
                    border-right: 1px solid rgba(255,255,255,0.1);
                    &.highlight{
                        color: #fff;
                    }
                }   
                .desc{
                    display: inline-block;
                    font-size: 0.30rem;
                    font-weight: 300;
                    line-height: 0.42rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.28rem;
                    box-sizing: border-box;
                    padding-left: 0.24rem;
                }           
            }
            .content-righ{
                flex:0 0 2.05rem;
                width: 2.05rem;
                .pay{
                    font-size: 0.24rem;
                    font-weight: 700;
                    line-height: 0.98rem;
                    height: 0.96rem;
                    color:#26333b;
                    background-color: #2b343c;
                    color:rgba(255,255,255,0.4);
                    text-align: center;
                    &.not-enough{
                        background: #2b333b;
                    }
                    &.enough{
                        background: #00b43c;
                        color: #fff;
                    }
                }
            }
        }
        // 购物车出入动画
        .movetop-enter-active, .movetop-leave-active {
            transition: all 0.8s;
            opacity: 1;
        }
        .movetop-enter, .movetop-leave-active {
            transform:translate(0,6rem);
            opacity: 0;
        }
        .shopcart-list{
            top:-5rem;
            position: absolute;
            left: 0;
            height: 6rem;
            z-index: -1;
            width: 100%;
            background: #fff;
            .list-header{
                height: 0.8rem;
                background: #f3f5f7;
                line-height: 0.8rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                padding: 0 0.36rem;
                font-weight: 200;
                overflow: hidden;
                .title{
                    float: left;
                    font-size: 0.28rem;
                }
                .empty{
                    float: right;
                    font-size: 0.24rem;
                    color:rgb(0,160,220);
                }
            }
            .list-content{
                padding: 0 0.36rem;
                max-height: 4.34rem;
                // background: #fff;
                overflow: hidden;
                .food{
                    position: relative;
                    padding: 0.24rem 0;
                    box-sizing: border-box;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .name{
                        line-height: 0.48rem;
                        font-size: 0.28rem;
                        color:rgb(7,17,27);
                    }
                    .price{
                        position: absolute;
                        right:1.8rem;
                        bottom:0.24rem;
                        line-height: 0.3rem;
                        font-size: 0.28rem;
                        font-weight: 700;
                        color:rgb(240,20,20);
                    }
                    .cartcoltrol-wrap{
                        position: absolute;
                        right: 0;
                        bottom: 0.12rem;
                    }
                }
            }
        }
    }
    .mask-enter-active, .mask-leave-active {
            transition: all 0.8s;
            opacity: 1;
        }
    .mask-enter, .mask-leave-active {
            opacity: 0;
        }
    .list-mask{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        background:rgba(7,17,27,0.6);
    }
</style>



